<template>
	<form action="" class="form_ty01" @submit.prevent="submitForm">
		<div class="row">
			<label for=""></label>
			<input type="text" v-model="subject">
		</div>
		<div class="row">
			<label for=""></label>
			<textarea name="" id="" cols="30" rows="10" v-model="content"></textarea>
		</div>
		<div class="row">
			<label for=""></label>
			<input type="file" @change="uploadFile" ref="uploadImageFile">
		</div>
		<img :src="image" alt="">
		<button type="submit" class="btn_ty01"></button>
	</form>
</template>

<script>
import bus from '@/utils/bus.js';

export default {
	data() {
		return {
			subject: '',
			content: '',
			image: '',
			uploadImageFile: '' 
		}
	},
	methods: {
		async submitForm() {
			console.log('submitform ');
			try {

				// upload file
				const formData = new FormData(); // formdata    ,    req.file   req.body 
				formData.append('uploadImage', this.uploadImageFile); //  key node.js upload.single('upLoadImage')   
				formData.append('subject', this.subject);
				formData.append('content', this.content);

				console.log(formData);
				
				// var response = await this.$http({
				// 	method: 'post',
				// 	url: '/posts/create',
				// 	data: {
				// 		subject: this.subject,
				// 		content: this.content
				// 	},
				// 	fd
				// });

				for (let key of formData.entries()){ 
					console.log(key[0] + ' '+key[1]) 
					}

				var response = await this.$http.post('/posts/create',formData, {
					headers: {
						'Content-Type': 'application/json'
					}
				});
				bus.$emit('show:toast', response.data.msg)
				// this.$router.push('/board/list');
			} catch(errer) {
				console.log('submitform method error : ' + errer);
			}


		},
		onFileChange(event) {
			// var files = event.target.files || event.dataTransfer.files;  event.dataTransfer.files  
			var files = event.target.files; // return FileList
			console.log(files);
			if(!files.length)
				return;

			this.createImage(files[0]);
		},
		createImage(file) {
			var reader = new FileReader();
			var vm = this;

			reader.onload = (e) => { // FileReader      load EventListener  function .
				vm.image = e.target.result;
			}
			reader.readAsDataURL(file); //    4  
			// reader.readAsText(file); 
		},
		uploadFile() {
			this.uploadImageFile = this.$refs.uploadImageFile.files[0];
		}
	}
}
</script>

